@import "../../Tools/General.scss";

  .menuItems {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    margin-top: 3rem;
  
    li {
      cursor: pointer;
      color: #b2a1a1;
      font-weight: 600; 
      padding:  0 30px 10px;
      letter-spacing: 8px;
    }
  }

.active {
  border-bottom: 2px solid $secondary;
}
@media screen and (min-width: 800px) {


  .menuItems  {

    margin-top: 0;

      
      flex-direction: row;
    }


}
